
.v-music{position:relative;font-family:Arial,Helvetica,sans-serif;-webkit-box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.2),0 1px 5px 0 rgba(0,0,0,0.12);box-shadow:0 2px 2px 0 rgba(0,0,0,0.14),0 3px 1px -2px rgba(0,0,0,0.2),0 1px 5px 0 rgba(0,0,0,0.12);display:inline-block;font-size:14px;width:520px;overflow:hidden
}
.v-music .v-theme{position:absolute;top:2px;right:2px;cursor:pointer
}
.v-music .v-theme i{color:#666
}
.v-music .v-search{position:absolute;top:2px;right:20px
}
.v-music .v-search i{position:absolute;top:3px;left:5px;font-size:12px;color:#666
}
.v-music .v-search input{font-size:12px;outline:none;-webkit-border-radius:10px;border-radius:10px;border:1px solid rgba(0,0,0,0.2);padding-left:18px;width:100px;color:#666
}
.v-music .v-pic{position:relative;display:inline-block;width:95px;height:95px;background-size:cover
}
.v-music .v-pic i{color:white;font-size:23px;cursor:pointer
}
.v-music .v-pic i.icon-play{position:absolute;top:30px;left:30%;font-size:35px;-webkit-animation:showPlay 0.2s ease-out;animation:showPlay 0.2s ease-out
}
.v-music .v-pic i.icon-pause{position:absolute;top:5px;left:5px;-webkit-animation:showPause 0.2s ease-out;animation:showPause 0.2s ease-out
}
@-webkit-keyframes showPlay{
from{position:absolute;top:5px;left:5px;font-size:23px
}
to{position:absolute;top:30px;left:30%;font-size:35px
}
}
@keyframes showPlay{
from{position:absolute;top:5px;left:5px;font-size:23px
}
to{position:absolute;top:30px;left:30%;font-size:35px
}
}
@-webkit-keyframes showPause{
from{position:absolute;top:30px;left:30%;font-size:35px
}
to{position:absolute;top:5px;left:5px;font-size:23px
}
}
@keyframes showPause{
from{position:absolute;top:30px;left:30%;font-size:35px
}
to{position:absolute;top:5px;left:5px;font-size:23px
}
}
.v-music .v-info{float:right;width:420px;height:95px;overflow:hidden;padding:10px 7px 5px 7px;-webkit-box-sizing:border-box;box-sizing:border-box
}
.v-music .v-info .v-title{overflow:hidden;white-space:nowrap;text-overflow:ellipsis;margin:0 0 13px 5px;-webkit-user-select:text;-moz-user-select:text;-ms-user-select:text;user-select:text;cursor:default;padding-bottom:2px
}
.v-music .v-info .v-title .song{font-size:15px
}
.v-music .v-info .v-title .singer{font-size:12px;color:#666
}
.v-music .v-info .v-lyr{height:45px;background:#fff;text-align:center;overflow:hidden;margin:-10px 0 7px
}
.v-music .v-info .v-lyr .lyr-list{-webkit-transition:-webkit-transform 0.4s ease-out;transition:-webkit-transform 0.4s ease-out;transition:transform 0.4s ease-out;transition:transform 0.4s ease-out, -webkit-transform 0.4s ease-out
}
.v-music .v-info .v-lyr p{font-size:12px;line-height:15px;color:#666;opacity:.4
}
.v-music .v-info .v-lyr p.on{opacity:1
}
.v-music .v-info .v-progress{position:relative;margin:0 0 0 5px;-webkit-box-flex:1;-webkit-flex:1;-ms-flex:1;flex:1
}
.v-music .v-info .v-progress .v-bar{float:left;width:63%;height:8px;cursor:pointer
}
.v-music .v-info .v-progress .v-line{width:100%;position:relative;height:2px;background:#cdcdcd
}
.v-music .v-info .v-progress .v-line-to{position:absolute;top:0;left:0;width:0;height:2px;background:#ad7a86;-webkit-transition:all 0.05s linear;transition:all 0.05s linear
}
.v-music .v-info .v-progress .v-line-to .v-point{position:absolute;top:0;right:5px;margin-top:-4px;margin-right:-10px;height:8px;width:8px;-webkit-border-radius:50%;border-radius:50%;background:#fff;cursor:pointer;border:1px solid #ad7a86
}
.v-music .v-info .v-progress .v-line-to .v-point:hover{background-color:#ad7a86
}
.v-music .v-info .v-progress .v-des{position:relative;margin-left:63%;width:37%;right:0;bottom:10px;height:17px;color:#999;font-size:11px;padding-left:7px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.v-music .v-info .v-progress .v-des span{line-height:17px;vertical-align:middle
}
.v-music .v-info .v-progress .v-des i{opacity:0.6;cursor:pointer
}
.v-music .v-info .v-progress .v-des i:hover{opacity:1
}
.v-music .v-info .v-progress .v-des i.icon-volume-max{font-size:18px;padding-left:2px;font-size:18px
}
.v-music .show-list{overflow:hidden
}
.v-music .show-list ol li{position:relative;height:32px;line-height:32px;list-style-type:armenian;padding:0 15px;font-size:12px;border-top:1px solid #e9e9e9;cursor:pointer;-webkit-transition:all .2s ease;transition:all .2s ease;overflow:hidden
}
.v-music .show-list ol li span.v-cur{position:absolute;width:3px;height:22px;left:0;top:5px;cursor:pointer;background:#ad7a86
}
.v-music .show-list ol li span.song{margin-left:10px
}
.v-music .show-list ol li span.singer{float:right
}
.v-music .show-list ol li.on,.v-music .show-list ol li:hover{background:#efefef
}
@media screen and (max-width: 520px){
.v-music{margin-left:1%;width:98%
}
.v-music .v-content .v-pic{width:20%
}
.v-music .v-content .v-info{width:80%
}
.v-music .v-content .v-progress .v-bar{width:50%
}
.v-music .v-content .v-progress .v-bar .v-line-to .v-point{width:10px;height:10px;margin-top:-5px
}
.v-music .v-content .v-progress .v-des{position:absolute;margin-left:0;right:-5px;bottom:-8px;width:auto
}
}
@media screen and (max-width: 375px){
.v-music .v-content .v-progress .v-bar{width:45%
}
}
@media screen and (max-width: 320px){
.v-music .v-content .v-progress .v-bar{width:40%
}
}



